import Vue from 'vue';
import Router from 'vue-router';
import HomePage from '../components/HomePage.vue';
import ServiceInfoPage from '../components/ServiceInfo.vue';
import ReservationPage from '../components/Reservation.vue';
import ReservationCheckPage from '../components/ReservationCheck.vue';
import FaqPage from '../components/FaqPage.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: HomePage,
      children: [
        {
          path: '',
          name: 'Home',
          component: HomePage
        }
      ]
    },
    {
      path: '/dashboard',
      component: () => import('../pages/AppDashboard.vue'),
      children: [
        {
          path: '',
          name: 'ServiceInfo',
          component: ServiceInfoPage
        },
        {
          path: 'reservation',
          name: 'Reservation',
          component: ReservationPage
        },
        {
          path: 'reservation-check',
          name: 'ReservationCheck',
          component: ReservationCheckPage
        },
        {
          path: 'faq',
          name: 'Faq',
          component: FaqPage
        },
      ]
    }
  ]
});
